    },
    data(){
        return {
            openI: false,
            sonNew: '0',
            photo_url: [
                require('@/assets/乐图/歌乐图-3.png'),
                require('@/assets/乐图/宫乐图.jpg'),
                require('@/assets/乐图/合乐图-3.png'),
                // require('@/assets/乐图/唐宫伎乐图-1.jpg'),
                // require('@/assets/乐图/燕乐.jpg'),
                require('@/assets/乐图/韩熙载-1.png'),
            ],
            photo_text: [
                '《歌乐图》',
                '《宫乐图》',
                '《合乐图》',
                // '《唐宫伎乐图》',
                // '《燕乐十二声》',
                '《韩熙载夜宴图》'
            ],
            photo_content: [
                '此卷描绘了南宋宫廷歌乐女伎演奏、排练的场景。画面中女伎、乐官和女童手持各种乐器于庭院中一字排开，均穿着南宋时期的典型服饰：九位女伎身材修长，穿着红色窄袖背子，高髻上饰以角状配饰；男性乐官佩戴朝天幞头，女童则戴簪花幞头。无论人物形象还是场景均为南宋社会文化生活的生动写照。技法上，设色细腻鲜艳，用笔精细而能够随物应形，通过不同的笔法表现不同物象的特定质感，如衣纹流畅、竹叶劲挺、树石沉雄，写实且富于意趣。画面构图疏朗简洁，节奏感强，整体上呈现出相当高的艺术水平。而以歌乐女伎为表现题材的作品在南宋人物画中极为少见，具有相当高的历史、艺术价值。',
                '《唐人宫乐图》为唐代佚名创作的绢本墨笔画，现藏于台北故宫博物院。画面描绘了一群宫中女眷围着桌案宴饮行乐的场面，共画十二位人物，其中贵妇十人，一个个高挽发髻，衣着华丽，姿态雍容，环案而坐，两个侍女则是站立长案边，在旁侍候，她们吹奏畅饮，好不热闹。画面以巨型方桌为中心，活动内容可分为品茗、奏乐和行酒令三个部分。',
                '《合乐图》绢本，水墨设色，五代南唐周文矩绘，纵41.9厘米，横184.2厘米，现藏于美国芝加哥艺术学院。这幅《合乐图》卷，相传便是周文矩仕女图中的精品。这幅画主要描绘古代女乐队在庭院里为皇宫贵族演奏的场景。画卷分为两部分，开卷先见一面居中的、上附华盖的建鼓，随后“拍”板、箫（左筚篥右尺八）、横笛、两杖鼓、方响（打击乐器，含十六块铁片）、筝（有十三弦）、箜篌（中式“竖琴”）和琵琶（手指直接拨奏）沿左右两侧顺次对称排开，共十九人，显然是个正在进行演奏的乐队。',
                '《韩熙载夜宴图》是五代十国时期南唐画家顾闳中的绘画作品，现存宋摹本，绢本设色，现藏于北京故宫博物院。《韩熙载夜宴图》描绘了官员韩熙载家设夜宴载歌行乐的场面。此画绘写的就是一次完整的韩府夜宴过程，即琵琶演奏、观舞、宴间休息、清吹、欢送宾客五段场景。整幅作品线条遒劲流畅，工整精细，构图富有想象力。'
            ],
            activeName: 'first',
            showBu: false,
        }
    },
