                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li1-text">
                                    <p>曲项琵琶梨形、曲项、四弦、横抱用拨子弹奏。这种琵琶起源于波斯（今伊朗），叫“乌德”，后经龟兹（新疆库车地区）传入中原。《隋书·音乐志》有下列记载：“先是周武帝〔在位公元561年—577年〕时，有龟兹人曰苏祗婆，从突厥皇后入国〔公元568年〕，周武帝公元568年聘突厥阿史那氏为皇后，随从皇后来了大批乐工，苏祗婆善弹琵琶，听其所奏，一均之中间有七声。因而问之，答云：父在西域知音。代相传习，调有七种。以其七调，勘校七声，冥若合符……”</p>
                                    <p>排箫，传统乐器，迄今发现的世界上最早的排箫是距今3000年的中国西周初期的骨排箫。古代的排萧以参差不齐的多个小竹管依其长短顺序并排，并用木框镶起来。排萧在历史上除单独演奏外，多用于声乐或歌舞伴奏、器乐合奏，民间已很少使用。</p>
                                </div>
                                <div class=" ps4-1li-video ps4-1li1-video">
                                    <video src="../assets/music/琵琶.mp3" controls="controls" style="width: 100%;height: 50px;"></video>
                                    <video src="../assets/music/清明追思(排箫).flac" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li> 
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/歌乐-1-2.jpg"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li1-text">
                                    <p>画中方形桌面上，摆放在两位女伎前面的是方响。方响，古称铜磬，又有铁板琴之称，是我国古代打击乐器。始于南北朝梁代。据《旧唐书》载：“梁有铜磬，盖今方响之类。方响，以铁为之，修八寸，广二寸，圆上方下。架如磬而不设业，倚于架上以代钟磬。人间所用者才三四寸。”北周之时，文献始载方响其名。《周正乐》载：“西凉清乐方响，一架十六枚，具黄钟、大吕二均声。”隋唐时期用于燕乐，后世用于宫廷雅乐。宋以后，方响在民间逐渐失传。20世纪70年代末，上海民族乐团和上海民族乐器一厂合作，在参阅古代文献和残存的唯一方响基础上研制成功新型方响，80年代初，又制成51音方响，可独奏、合奏或为歌、舞伴奏，已正式列入民族乐队编制中。</p>
                                    <p>鼓是我国传统的打击乐器，按《礼记·明堂位》的记载，在很早的传说中，“伊耆氏”之时就已有“土鼓”，即陶土作成的鼓。由于鼓有良好的共鸣作用，声音激越雄壮而传声很远，所以很早就被华夏祖先作为军队上助威之用。相传黄帝征服蚩尤的涿鹿之战中，“黄帝杀夔，以其皮为鼓，声闻五百”（《太平御览》卷五八二《帝王世纪》）。</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li1-video">
                                    <video src="../assets/music/方响.mp3" controls="controls" style="width: 100%;height: 50px;"></video>
                                    <video src="../assets/music/金鼓点兵（建鼓）.mp3" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/歌乐-1-3.jpg"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li1-text">
                                    <p>笛子，原名为“涤”，古时称之为“邃”或“横吹”，因大多数笛子均用天然的竹材制作而成，所以又称之为“竹笛”，距今已有八、九千年的历史，是中国较为古老的传统吹奏乐器，具有浓郁的中国特色。
笛子构造简单，种类繁多，大小不一，通常以竹管作为管身，再贴上笛膜，通过气流振动笛膜来发声，是一种表现力较强，演奏风格丰富的乐器。它音域为两个八度零一个音，音色宽松、浑厚、圆润、明亮，吹奏出的声音清脆悦耳、圆润明亮宛如龙吟，被古人誉为“龙吟凤鸣”。有独奏、伴奏、合奏等多种艺术表现形式，是民间音乐、地方戏曲、民族歌舞的重要伴奏乐器，也是中国民族管弦乐队中的主要成员，在民族乐队中起着重要的作用。
笛子在中国各地域均有发展，并形成了具有特色地域代表的南北两大派系，主要流行于中国的汉族地区，除此之外在蒙古族、维吾尔族、藏族等少数民族中也有使用，演奏风格各异，均具有自身的特点。竹笛演奏以中国传统艺术文化作为思想根基，以世界文化交融为背景， 并融合了多种民族乐器风格，这不仅为中国竹笛演奏注入新的元素和活力，也丰富了世界音乐体系，同时让观众通过感官来欣赏这一表演艺术，产生情感交流，以此获得审美享受。</p>
                                </div>
                                <div class="ps4-1li-video  ps4-1li1-video">
                                    <video src="../assets/music/鹧鸪飞 (竹笛).flac" controls="controls" style="width: 100%;height: 50px;"></video>     
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/歌乐-1-4.jpg"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li1-text">
                                    <p>拍板，中国碰奏体鸣乐器。又称檀板、绰板，简称板。用于戏曲、曲艺和器乐合奏。古时由西北传入中原。唐代用于散乐，宋代用于民间说唱、民间器乐、教坊大乐和马后乐，元代用于宫廷音乐和杂剧，明、清用于宫廷和民间音乐。现代拍板多以3块长方形紫檀 、红木或黄杨木板组成。板长18～20厘米，宽4～6厘米。前两块板用丝弦缠绕，然后用布带与后面的单块木板连结。以左手执后板，撞击前两块木板发声。拍板常与板鼓合用，由鼓手兼操。拍板也流传于蒙古族和满族中。用红木、紫檀、花梨或荔木制作，历代因使用目的不同，板的数量也不一致。通常由五六块板组成，最多者九板，最少者三四板。满族的拍板多为两板或三(两大一小)板，除木制者外，还有用铁片制成的。每块拍板的上端均钻有两个小孔，用细皮条或丝绳串联，下端可自由开合。传世拍板有故宫博物院藏清代六联拍板(图清代留联拍板)，用红木制成，每板长40.5厘米、上宽7.9厘米、下宽8.5厘米、厚1.8厘米，两端均呈钝三角形，为清代乾隆年间(公元1736年—1795年)制品，此拍板与五代前蜀王建墓乐舞石刻中的拍板完全相同。 现代福建南音所用五联拍板，多用荔木制作，中间稍小的三板长26厘米～30厘米、宽8厘米、厚2厘米，外侧略大的两板长30厘米～34厘米、宽10厘米、厚3厘米，每板的两端和外侧两板的外面均为半圆形。</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li1-video">
                                    <video src="../assets/music/打板铃音（拍板）.aac" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                </ul>
            </div>
            <div class="ps4-1" v-if="message === 2">
                <ul>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/宫乐图-1-1.png"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li2-text">
                                    <p>胡笳（Hú Jiā），是边棱气鸣乐器，民间又称潮尔、冒顿潮尔。胡笳，我国古代北方民族的一种乐器，形似笛子。 [1]
胡笳可用于独奏、器乐合奏或乐队伴奏，是富有浓郁民族色彩的吹奏乐器。琴曲中有《大胡笳》《小胡笳》《胡笳十八拍》琴歌等版本。曲调虽然各有不同，但都反映了蔡文姬思念故乡而又不忍骨肉分离的极端矛盾的痛苦心情。音乐委婉悲伤，撕裂肝肠。《胡笳十八拍》相传是蔡琰所作，但未得到可靠考证。</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li2-video">
                                    <video src="../assets/music/胡笳十八拍（胡笳）.mp3" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/宫乐图-1-2.png"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li2-text">
                                    <p>琵琶，是中国传统弹拨乐器，已有两千多年的历史。最早被称为“琵琶”的乐器大约在中国秦朝出现。“琵琶”二字中的“珏”意为“二玉相碰，发出悦耳碰击声”，表示这是一种以弹碰琴弦的方式发声的乐器。
其名“琵”、“琶”是根据演奏这些乐器的右手技法而来的。也就是说琵和琶原是两种弹奏手法的名称，琵是右手向前弹，琶是右手向后挑。在唐朝以前，琵琶也是汉语里对所有鲁特琴族（又称琉特属）弹拨乐器的总称。中国琵琶更传到东亚其他地区，发展成现时的日本琵琶、朝鲜琵琶和越南琵琶。
中国近代汉族民族音乐史上有“海派”（浦东派）琵琶、“浙派”（平湖派）琵琶、“汪派 ”（汪派）等多流派。</p>
                                </div>
                                <div class="ps4-1li-video  ps4-1li2-video">
                                    <video src="../assets/music/琵琶.mp3" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/宫乐图-1-3.png"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li2-text">
                                    <p>古筝，弹拨弦鸣乐器，又名汉筝、秦筝，是汉民族古老的民族乐器，流行于中国各地。经过千百年的发展，主要形成了客家筝、潮州筝、山东筝、河南筝四大流派。常用于独奏、重奏、器乐合奏和歌舞、戏曲、曲艺的伴奏。因音域宽广，音色优美动听，演奏技巧丰富，表现力强，而被称为“众乐之王”，亦称为“东方钢琴”，是中国独特的、重要的民族乐器之一。
古筝的结构由面板、雁柱、琴弦、前岳山、弦钉、调音盒、琴足、后岳山、侧板、出音口、底板、穿弦孔组成。形制为长方形木质音箱，弦架“筝柱”（即雁柱）可以自由移动，一弦一音，按五声音阶排列。
<br>远在战国时代的秦国境内（陕西省），筝就已经流行，当时称为“秦筝”。此后，又流传到了河南、山东、广东、浙江、内蒙古等地。由于历史的变迁和地域的影响而造成了地方风格、演奏技法等的不同，并因此形成了当今各种不同的古筝流派，使古筝的发展日趋繁荣。
<br>筝在汉、晋以前设十二弦（见魏·阮璃《等筝赋》“弦有十二”）。
<br>隋唐时期，筝由十二弦增至十三弦（见唐·岑参《秦筝歌，送外甥萧正归京》“汝不闻秦筝声最苦，五色缠弦十三柱”）。</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li2-video">
                                    <video src="../assets/music/云水禅心 (古筝).flac" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/宫乐图-1-4.png"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li2-text">
                                    <p>笙属于簧片乐器族内的吹孔簧鸣乐器类，是世界上现存大多数簧片乐器的鼻祖。发音清越、高雅，音质柔和，歌唱性强，具有中国民间色彩。<br>笙是簧管乐器，历史悠久，能奏和声。它以簧、管配合振动发音，簧片能在簧框中自由振动，是世界上最早使用自由簧的乐器。<br>《尔雅·释乐》记载：“大笙谓之巢，小者谓之和。”
在我国古代乐器分类中，笙为匏类乐器。《诗经》的《小雅.鹿鸣》写道：“我有嘉宾，鼓瑟吹笙。吹笙鼓簧，承筐是将”，可见笙在当时已经很流行了。</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li2-video">
                                    <video src="../assets/music/凤凰展翅（笙）.mp3" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                    
