                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li2-text">
                                    <p>拍板，中国碰奏体鸣乐器。又称檀板、绰板，简称板。用于戏曲、曲艺和器乐合奏。古时由西北传入中原。唐代用于散乐，宋代用于民间说唱、民间器乐、教坊大乐和马后乐，元代用于宫廷音乐和杂剧，明、清用于宫廷和民间音乐。现代拍板多以3块长方形紫檀 、红木或黄杨木板组成。板长18～20厘米，宽4～6厘米。前两块板用丝弦缠绕，然后用布带与后面的单块木板连结。以左手执后板，撞击前两块木板发声。拍板常与板鼓合用，由鼓手兼操。拍板也流传于蒙古族和满族中。用红木、紫檀、花梨或荔木制作，历代因使用目的不同，板的数量也不一致。通常由五六块板组成，最多者九板，最少者三四板。满族的拍板多为两板或三(两大一小)板，除木制者外，还有用铁片制成的。每块拍板的上端均钻有两个小孔，用细皮条或丝绳串联，下端可自由开合。传世拍板有故宫博物院藏清代六联拍板(图清代留联拍板)，用红木制成，每板长40.5厘米、上宽7.9厘米、下宽8.5厘米、厚1.8厘米，两端均呈钝三角形，为清代乾隆年间(公元1736年—1795年)制品，此拍板与五代前蜀王建墓乐舞石刻中的拍板完全相同。 现代福建南音所用五联拍板，多用荔木制作，中间稍小的三板长26厘米～30厘米、宽8厘米、厚2厘米，外侧略大的两板长30厘米～34厘米、宽10厘米、厚3厘米，每板的两端和外侧两板的外面均为半圆形。</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li2-video">
                                    <video src="../assets/music/打板铃音（拍板）.aac" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                </ul>
            </div>
            <div class="ps4-1" v-if="message === 3">
                <ul>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/合乐图-1-1.jpg"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li3-text">
                                    <p>箜篌（kōng hóu），又称坎侯或空侯，是中国传统拨弦乐器。在古代除宫廷乐使用外，在民间也流传。在古代有卧箜篌、竖箜篌、凤首箜篌三种形制，琴弦一般系在敞开的框架上，用手指拨弹。<br>箜篌在古代有卧箜篌、竖箜篌、凤首箜篌三种形制。《史记·封禅书》：“于是塞南越，祷祠太一，后土，始用乐舞，益召歌儿，作二十五弦及空侯琴瑟自此起。”《史记·孝武本纪》：“于是塞南越，祷祠泰一，后土，始用乐舞，益召歌儿，作二十五弦及箜篌瑟自此起。</p>
                                    <p>古筝，弹拨弦鸣乐器，又名汉筝、秦筝，是汉民族古老的民族乐器，流行于中国各地。经过千百年的发展，主要形成了客家筝、潮州筝、山东筝、河南筝四大流派。常用于独奏、重奏、器乐合奏和歌舞、戏曲、曲艺的伴奏。因音域宽广，音色优美动听，演奏技巧丰富，表现力强，而被称为“众乐之王”，亦称为“东方钢琴”，是中国独特的、重要的民族乐器之一。</p>
                                    <p>方响，又称方晌、铜磬，是中国传统打击乐器，南朝梁代(502-557)出现，是中国古代很有艺术特色并具有固定音高的敲击乐器。 <br>后为隋、唐燕乐中常用的乐器。它通常由十六块铁板根据音高顺序排列而成，用小铁锤或木锤敲击发音。</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li3-video">
                                    <video src="../assets/music/高山流水 (箜篌).flac" controls="controls" style="width: 100%;height: 50px;"></video>
                                    <video src="../assets/music/云水禅心 (古筝).flac" controls="controls" style="width: 100%;height: 50px;"></video>
                                    <video src="../assets/music/方响.mp3" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/合乐图-1-2.jpg"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li3-text">
                                    <p>尺八，中国传统乐器，唐宋时期传入日本。竹制，内涂朱砂拌大漆填充（地）外切口，今为五孔（前四后一），属边棱振动气鸣吹管乐器，以管长一尺八寸而得名，其音色苍凉辽阔，又能表现出空灵、恬静的意境。尺八的声音可高可低。音色可脆如银铃，也可细腻如丝。这就要求吹奏者有相当的把握能力。</p>
                                    <p>腰鼓是中国汉族古老的民族打击乐器。腰鼓最初流行于陕北的安塞、横山、米脂、子洲等地，以安塞、横山最为普及，后来在全中国发展流行，是人民群众喜闻乐见的一种表演形式。腰鼓在民间十分盛行，人们跳着腰鼓舞，变换队形，或行走或在一个场地内边舞边敲。同时，也用于民间秋歌舞。在表演的形式和技巧上，变化极为丰富。腰鼓的演奏与舞蹈动作紧密结合，称为腰鼓舞。腰鼓依据不同的风格韵律原有文、武之分。文腰鼓轻松愉快、潇洒活泼，动作幅度小，类似秋歌的风格。武腰鼓则欢快激烈、粗奔放，并有较大的踢打、跳跃和旋转动作。尤其是鼓手的腾空飞跃技巧，给人们以英武的感觉。</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li3-video">
                                    <video src="../assets/music/夜明（尺八）.mp3" controls="controls" style="width: 100%;height: 50px;"></video>
                                    <video src="../assets/music/安塞腰鼓（腰鼓）.mp3" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/合乐图-1-3.jpg"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li3-text">
                                    <p>筚篥，也称管子，属吹管乐器。它的管身多为木制，上面开有八孔，管口插一苇制的哨子而发音。它是由古代龟兹人发明创造，名称也是从古龟兹语的译音而来。最早文献见于南朝何承天的《篡文》：必栗者，羌胡乐器名也。而唐代的段安节则在《乐府杂录》中为我们明确了它的发源地：筚篥者，本龟兹国乐也。亦名悲篥，有类于茄。龟兹，即新疆的库车县。</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li3-video">
                                    <video src="../assets/music/大漠抒怀 (筚篥).flac" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/合乐图-1-4.jpg"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li3-text">
                                    <p>琵琶，是中国传统弹拨乐器，已有两千多年的历史。最早被称为“琵琶”的乐器大约在中国秦朝出现。“琵琶”二字中的“珏”意为“二玉相碰，发出悦耳碰击声”，表示这是一种以弹碰琴弦的方式发声的乐器。
<br>其名“琵”、“琶”是根据演奏这些乐器的右手技法而来的。也就是说琵和琶原是两种弹奏手法的名称，琵是右手向前弹，琶是右手向后挑。在唐朝以前，琵琶也是汉语里对所有鲁特琴族（又称琉特属）弹拨乐器的总称。中国琵琶更传到东亚其他地区，发展成现时的日本琵琶、朝鲜琵琶和越南琵琶。</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li3-video">
                                    <video src="../assets/music/琵琶.mp3" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/合乐图-1-5.jpg"
                        />
                        </div>
                        </template>
                        <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li3-text">
                                    <p>笙，是起源于我国的汉族古老的吹奏乐器，它是世界上最早使用自由簧的乐器，并且对西洋乐器的发展曾经起过积极的推动作用<br>笙属于簧片乐器族内的吹孔簧鸣乐器类，是世界上现存大多数簧片乐器的鼻祖。发音清越、高雅，音质柔和，歌唱性强，具有中国民间色彩。<br>笙是簧管乐器，历史悠久，能奏和声。它以簧、管配合振动发音，簧片能在簧框中自由振动，是世界上最早使用自由簧的乐器。史记补三皇本记记载：“女娲氏风姓，有神圣之德，代宓仪、立号曰女希氏，作笙黄。”可之，女娲当时并无笙，只作笙中簧，此时的笙簧许是以竹、木片所制，只可发出高低不同的音。到唐、虞、夏、商诸朝代，发展以数根的竹簧之管参差插入葫芦之干壳内制成。远在3000多年前的商代，我国就已有了笙的雏型。在出土的殷（公元前1401～前1122）墟甲骨文中有“和”的记载。“和”即是后世小笙的前身。《尔雅·释乐》记载：“大笙谓之巢，小者谓之和。”在我国古代乐器分类中，笙为匏类乐器。</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li3-video">
                                    <video src="../assets/music/凤凰展翅（笙）.mp3" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                            
                        </a-card>
                    </li>
                </ul>
            </div>
            <div class="ps4-1" v-if="message === 4">
                <ul>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/韩熙载-2-1.png"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li4-text">
                                    <p>鼓是一种打击乐器，在坚固的且一般为圆桶形的鼓身的一面或双面蒙上一块拉紧的膜。鼓可以用手或鼓杵敲击出声。鼓在非洲的传统音乐以及在现代音乐中是一种比较重要的乐器，有的乐队完全由以鼓为主的打击乐器组成。除了作为乐器外鼓在古代许多文明中还用来传播信息。
                    <br>鼓是我国传统的打击乐器，按《礼记·明堂位》的记载，在很早的传说中，“伊耆氏”之时就已有“土鼓”，即陶土作成的鼓。由于鼓有良好的共鸣作用，声音激越雄壮而传声很远，所以很早就被华夏祖先作为军队上助威之用。相传黄帝征服蚩尤的涿鹿之战中，“黄帝杀夔，以其皮为鼓，声闻五百”（《太平御览》卷五八二《帝王世纪》）。</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li4-video">
                                    <video src="../assets/music/金鼓点兵（建鼓）.mp3" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/韩熙载-2-2.png"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li4-text">
                                    <p>曲项琵琶梨形、曲项、四弦、横抱用拨子弹奏。这种琵琶起源于波斯（今伊朗），叫“乌德”，后经龟兹（新疆库车地区）传入中原。乌德，传到西方后渐渐演变成吉它、曼多林、琉特琴等乐器。而传入中国后是另外一种面貌。这种外来乐器受到了中国悠久的古老文化的浸润，它在中国的发展离不开中国音乐的滋养。
《隋书·音乐志》有下列记载：“先是周武帝〔在位公元561年—577年〕时，有龟兹人曰苏祗婆，从突厥皇后入国〔公元568年〕，周武帝公元568年聘突厥阿史那氏为皇后，随从皇后来了大批乐工，苏祗婆善弹琵琶，听其所奏，一均之中间有七声。因而问之，答云：父在西域知音。代相传习，调有七种。以其七调，勘校七声，冥若合符……”</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li4-video">
                                    <video src="../assets/music/琵琶.mp3" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
                            </template>
                            </a-card-meta>
                        </a-card>
                    </li>
                    <li class="ps4-1li">
                        <a-card hoverable>
                        <template #cover>
                        <div
                        :style="{
                        height: '80%',
                        overflow: 'hidden',
                        }"
                        >
                        <img
                        :style="{ width: '100%', transform: 'translateY(-20px)' }"
                        alt="dessert"
                        src="../assets/乐图/韩熙载-2-3.png"
                        />
                        </div>
                        </template>
                            <a-card-meta >
                            <template #description>
                                <div class="ps4-1li-text ps4-1li4-text">
                                    <p>笛子，原名为“涤”，古时称之为“邃”或“横吹”，因大多数笛子均用天然的竹材制作而成，所以又称之为“竹笛”，距今已有八、九千年的历史，是中国较为古老的传统吹奏乐器，具有浓郁的中国特色。
笛子构造简单，种类繁多，大小不一，通常以竹管作为管身，再贴上笛膜，通过气流振动笛膜来发声，是一种表现力较强，演奏风格丰富的乐器。它音域为两个八度零一个音，音色宽松、浑厚、圆润、明亮，吹奏出的声音清脆悦耳、圆润明亮宛如龙吟，被古人誉为“龙吟凤鸣”。有独奏、伴奏、合奏等多种艺术表现形式，是民间音乐、地方戏曲、民族歌舞的重要伴奏乐器，也是中国民族管弦乐队中的主要成员，在民族乐队中起着重要的作用。</p>
                                </div>
                                <div class="ps4-1li-video ps4-1li4-video">
                                    <video src="../assets/music/鹧鸪飞 (竹笛).flac" controls="controls" style="width: 100%;height: 50px;"></video>
                                </div>
